.App {
  font-family: Play, Arial, sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  background-color: beige;
  height: 100vh;
  width: 100vw;
}

h1 {
  text-align: center;
  padding-top: 20px;
  font-size: 36px;
  color: #333;
}

h2 {
  text-align: center;
  margin-top: 20px;
  font-size: 24px;
  color: #555;
}

button {
  margin: 5px;
  padding: 12px 24px;
  font-size: 18px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

#result {
  font-size: 24px;
  margin-top: 15px;
  color: #555;
}

#score {
  font-size: 24px;
  margin-top: 15px;
  color: #000;
}

.choices{
  display: flex;
  justify-content: center;
}

.table {
  max-width: 70%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  border: 1px solid #000;
  border-collapse: collapse;
}

.win{
  color: rgb(70, 203, 70);
}

.lose{
  color: red;
}

.table td {
  background-color: beige;
  border: 1px solid #000;
}

@media screen and (max-width: 600px) {
  button {
      width: 50%;
      padding: 10px;
      font-size: 14px;
  }

  #result {
      font-size: 14px;
      margin-top: 15px;
  }
  
  #score {
      font-size: 16px;
      margin-top: 15px;
  }

  .table {
      max-width: 90%;
      font-size: 13px;
  }

  h1 {
      padding-top: 15px;
  }

  h2 {
      margin-top: 15px;
  }
}

@media screen and (max-width: 375px) {
  button {
      width: 50%;
      padding: 10px;
      font-size: 10px;
  }
  
  #result {
      font-size: 11px;
      margin-top: 15px;
  }
  
  #score {
      font-size: 16px;
      margin-top: 15px;
  }

  .table {
      max-width: 95%;
      font-size: 11px;
  }
}
